@charset "utf-8";
.sustain_title_cont{
    max-width: 1280px;
    margin: 0 auto;
    margin-bottom: 40px;
}
.sustain_title_cont>h2{
    text-align: center;
    margin-bottom: 40px;
}
.sustain_text_cont{
    max-width: 1064px;
    margin: 0 auto;
    margin-bottom: 80px;
    padding-top: 40px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
}
.sustain_title_cont .subtitle-section{
    background: url('../images/main/sustainability.jpg') no-repeat center bottom;
}
.sustain_title_cont .subtitle-section .sub-text{
    color:#fff;
}
.sustain_title_cont .subtitle-section .sub-text a{
    color:#333;
}
.sustain_text_cont .img_cont{
    width: 50%;
    height: 300px;
    background: url('../images/main/togather.jpg') no-repeat center center;
    background-size: cover;
    border-radius: 14px;
    overflow: hidden;
}
.sustain_text_cont .text_cont{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sustain_project_cont{
    max-width: 1064px;
    margin: 0 auto;
    margin-bottom: 80px;
}
.sustain_project_cont h3{
    margin-bottom: 28px;
}
.project_cont_wrap{
    display: flex;
    justify-content: space-between;
}
.project_cont_wrap .basic-card-section{
    width: 32%;
}
.project_cont_wrap .basic-card-section img{
    width: 100%;
}
.sustain_report_cont{
    margin-bottom: 80px;    
}
.sustain_report_cont .cont01{
    border-top: 2px solid var(--k800-color);
    border-bottom: 2px solid var(--k800-color);
}
.sustain_report_cont .cont02{
    border-bottom: 2px solid var(--k800-color);
}
.sustain_tab_cont{
    max-width: 1064px;
    margin: 0 auto;
    margin-bottom: 80px;
}